<template>
  <div class="content-wrap-box">
    <el-card class="title-card">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item> 
      </el-breadcrumb>
    </el-card>
    <div class="router-view-wrap" v-loading="pageLoading">
      <!--  -->
      <div class="router-view-content">
        <el-row class="padding-row" v-if="common.checkAuthority('ArticleSetting-tree', true)">
          <el-col :span="7" v-loading="pageLoading">
            <div><el-button type="primary" class="fill-width" size="mini" :disabled="!common.checkAuthority('ArticleSetting-add', true)" @click="addTree(null)"></el-button></div>
            <el-tree
              v-if="treeData"
              :data="treeData"
              :props="defaultProps"
              node-key="id"
              @node-click="handleNodeClick"
              default-expand-all
              :expand-on-click-node="false"
              draggable
              @node-drag-end="handleDragEnd"
              :allow-drop="allowDrop"
              >
              <span class="fill-width" slot-scope="{ node, data }">
                <div @mouseenter="tempId = data.id" @mouseleave="tempId = null">
                  <span :class="{'text-primary' : activeId == data.id }" >
                    <!-- <i class="el-icon-circle-check" v-if="activeId == data.id"></i> -->
                    {{ node.label }}
                    <span v-if="tempId == data.id">
                      <el-button type="text" v-if="!data.parent_id && common.checkAuthority('ArticleSetting-add', true)"  @click.stop="addTree(data)"><i class="fa fa-plus text-primary" title=""></i></el-button>
                      
                      <el-button v-if="common.checkAuthority('ArticleSetting-add', true)" type="text" @click.stop="addTree(data, 'prev')"><i class="fa fa-arrow-up text-primary" title=""></i></el-button>
                      
                      <el-button v-if="common.checkAuthority('ArticleSetting-add', true)" type="text" @click.stop="addTree(data, 'next')"><i class="fa fa-arrow-down text-primary" title=""></i></el-button>                      
                      
                      <el-button type="text" v-if="common.checkAuthority('ArticleSetting-edit', true)" @click.stop="editTree(data)"><i class="fa fa-pencil-square-o text-primary" title=""></i></el-button>
                      
                      <el-button type="text" v-if="!(data.childrens && data.childrens.length > 0) && common.checkAuthority('ArticleSetting-delete', true)" @click.stop="deleteTree(data)"><i class="fa fa-trash-o text-danger" title=""></i></el-button>
                    </span>
                  </span>
                </div>
               </span>
            </el-tree>
          </el-col>
          <el-col v-if="common.checkAuthority('ArticleSetting-listArticle', true)" :span="17">
            <div v-if="activeNode.parent_id">
              <el-table
                :data="tableData.rows"
                :show-header="true">
                <el-table-column
                  prop="article_name"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="authorName"
                  label=""
                  width="180">
                </el-table-column>
                <el-table-column
                  label=""
                  width="180"
                  fixed="right">
                  <template slot-scope="scope">
                    <el-button type="default" size="mini" :disabled="!common.checkAuthority('ArticleSetting-detailArticle', true)" @click="getArticleDetail(scope.row, 'detail')"> </el-button>
                    <el-button type="danger" size="mini" :disabled="!common.checkAuthority('ArticleSetting-deleteArticle', true)" @click="deleteArticle(scope.row)"> </el-button>
                    <!-- <el-button type="primary" size="mini" @click="getArticleDetail(scope.row, 'edit')"> </el-button> -->
                  </template>
                </el-table-column>
              </el-table>
            </div>
            <div v-else class="text-center text-info">  </div>
          </el-col>
          <el-col v-else :span="17" class="text-center text-info">  </el-col>
        </el-row>
        <div v-else class="text-center text-info">  </div>
      </div>
      <!--  -->
      <div class="router-view-bottom" v-if="common.checkAuthority('ArticleSetting-listArticle', true)">
        <!--  -->
        <div>
          <el-button type="primary" size="mini" :disabled="!common.checkAuthority('ArticleSetting-addArticle', true) || !this.activeNode.parent_id" @click="showEditDialog(false)"></el-button>
        </div>
        <!--  -->
        <el-pagination
          background
          layout="total, sizes, prev, pager, next"
          :current-page="pageNum"
          :page-size="pageSize"
          :total="tableData.total"
          @size-change="pageSizeChange"
          @current-change="pageCurrentChange">
        </el-pagination>
      </div>
      <div v-else class="router-view-bottom"><div></div></div>
    </div>

    <el-dialog title="" :visible.sync="dialogTypeVisible" width="30vw">
      <el-form ref="typeForm" :model="typeForm" :rules="typeFormRules" label-width="100px">
        <el-form-item label="" prop="name"><el-input v-model="typeForm.name"></el-input></el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="typeForm.type" class="fill-width">
            <el-option :value="1" label=""></el-option>
            <el-option :value="2" label=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogTypeVisible = false"> </el-button>
        <el-button type="primary" @click="addTreeSubmit" :loading="pageLoading"> </el-button>
      </div>
    </el-dialog>

    <el-dialog :title="`${activeNode.name} - `" :visible.sync="dialogArticleEditVisible" width="80vw">
      <el-form :model="editorForm" label-width="100px">
        <el-form-item label=""><el-input v-model="editorForm.article_name"></el-input></el-form-item>
        <el-form-item label=""><div ref="editor"></div></el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogArticleEditVisible = false"> </el-button>
        <el-button type="primary" @click="submitArticle" :loading="pageLoading"> </el-button>
      </div>
    </el-dialog>
    
    <el-dialog title="" :visible.sync="dialogArticleDetailVisible" width="80vw">
      <el-form :model="editorForm" label-width="100px">
        <el-form-item label=""><div>{{editorForm.article_name}}</div></el-form-item>
        <el-form-item label=""><div v-html="editorForm.article_content"></div></el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogArticleDetailVisible = false" :loading="pageLoading"> </el-button>
        <el-button type="primary" :disabled="!common.checkAuthority('ArticleSetting-editArticle', true)" @click="changeToEdit"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  import E from "wangeditor"
  export default {
    data() {
      return {
        pageLoading: false,
        treeData:    [],
        activeId:  null, // id
        activeCode:  null, // code
        tempId:    null, //id
        defaultProps: {
          children: 'childrens',
          label: 'name'
        },

        tableData: {
          rows: [],
          total: 0
        },
        pageSize: 10,
        pageNum: 1,


        dialogTypeVisible: false,
        typeForm: {
          name: null,
          type: 1, //1 2
        },
        typeFormRules: {
          name: [{required: true, message: "", trigger: "blur"}],
          type: [{required: true, message: "", trigger: "change"}],
        },
        insertMode: false, //  

        dialogArticleEditVisible: false,
        editorForm: {  // &, 
          article_name:    "",
          article_content: ""
        },
        operateType: "new", // new  edit 
        dialogArticleDetailVisible: false,

        activeNode: {},
        notSecond: true, // true  
      };
    },
    mounted () {
      if(this.common.checkAuthority('ArticleSetting-tree', true)){
        this.initArticleTree()
      }
    },
    watch: {
      activeNode: {
        handler (newV,oldV) {
          console.log(newV)
          if(newV && !!newV.parent_id) {
            this.notSecond = false
          } else {
            this.notSecond = true
          }
        },
        deep: true
      }
    },
    methods: {
      /**
       * , 
       * @return {[type]} [description]
       */
      initArticleTree() {
        this.pageLoading = true
        let config = {
          method: "get",
          url: `${this.common.basePath}/article_type/tree`
        }
        this.axios(config).then((response) => {
          this.treeData   = response.data.data
          this.activeId   = response.data.data[0].id
          this.activeNode = response.data.data[0]

          if(this.common.checkAuthority('ArticleSetting-listArticle', true)){
            this.getArticleList(response.data.data[0].code)
          }

          this.pageLoading = false
        }).catch((errorMsg) => {
          this.pageLoading = false
          this.$message.error(errorMsg)
        })
      },
      /**
       * 
       * @param {[type]} parentId [description]
       */
      addTree(data, type) {
        this.insertMode = false
        this.operateType           = "new"
        this.dialogTypeVisible     = true
        this.typeForm = {
          name: null,
        }
        data ? this.typeForm.parent_id = data.id : this.typeForm.parent_id = null //data 

        if(type) {
          this.insertMode = true
          this.typeForm.parent_id = data.parent_id
          if(type == "prev") {
            this.typeForm.array_number = data.array_number
          } else {
            this.typeForm.array_number = data.array_number + 1
          }
        }
        this.$nextTick(_ => {
          this.$refs.typeForm.clearValidate()
        })
      },
      /**
       * (, )
       * @param  {[type]} data [description]
       * @return {[type]}      [description]
       */
      // addTree(data) {
      //   this.operateType = "new"
      //   this.dialogTypeVisible = true
      //   this.typeForm.name     = null
      // },
      /**
       * 
       * @param {[type]} parentId [description]
       */
      editTree(data) {
        this.operateType = "edit"
        this.dialogTypeVisible = true
        this.typeForm = JSON.parse(JSON.stringify(data))
        this.$nextTick(_ => {
          this.$refs.typeForm.clearValidate()
        })
      },
      /**
       * /
       * @param {[type]} parentId [description]
       */
      addTreeSubmit() {
        
        let url = ""
        if(this.insertMode) {
          url = `${this.common.basePath}/article_type/tree/add`
        } else {
          url = `${this.common.basePath}/article_type`
        }
        let config = {
          method: this.operateType == "new" ? "post" : "put",
          url: this.operateType == "new" ? url : `${this.common.basePath}/article_type/${this.typeForm.id}` ,
          data: this.typeForm
        }
        // data ? config.parent_id = data.parent_id : null
        this.$refs.typeForm.validate(valid => {
          if(valid) {
            this.pageLoading = true
            this.axios(config).then((response) => {
              this.$message.success(response.data.meta.message)
              this.dialogTypeVisible = false
              this.pageLoading = false
              this.initArticleTree()
            }).catch((errorMsg) => {
              this.dialogTypeVisible = false
              this.pageLoading = false
              this.$message.error(errorMsg)
            })
          }
        })
        
      },
      
      /**
       * 
       * @param {[type]} parentId [description]
       */
      deleteTree(data) {
        this.typeForm = JSON.parse(JSON.stringify(data))

        this.$confirm("", "?", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(_ => {
          let config = {
            method: "patch",
            url: `${this.common.basePath}/article_type/delete/${this.typeForm.id}` ,
          }
          this.pageLoading = true
          this.axios(config).then((response) => {
            this.$message.success(response.data.meta.message)
            this.dialogTypeVisible = false
            this.pageLoading = false
            this.initArticleTree()
          }).catch((errorMsg) => {
            this.dialogTypeVisible = false
            this.pageLoading = false
            this.$message.error(errorMsg)
          })
        }).catch(_ => {})
      },
      /**
       *  
       * @param  {[type]} data [description]
       * @return {[type]}      [description]
       */
      handleNodeClick(node, data) {
        this.activeId   = node.id
        this.activeCode = node.code
        this.activeNode = node
        if(this.common.checkAuthority('ArticleSetting-listArticle', true)) {
          this.getArticleList(this.activeCode);
        }
      },
      /**
       * code
       * @return {[type]} [description]
       */
      getArticleList(code) {
        this.pageLoading = true
        let config = {
          method: "get",
          url: `${this.common.basePath}/article`,
          params: {
            type:     this.activeId,
            pageNum:  this.pageNum,
            pageSize: this.pageSize,
          }
        }
        this.axios(config).then((response) => {
          this.tableData = response.data.data
          this.pageLoading = false
        }).catch((errorMsg) => {
          this.pageLoading = false
          this.$message.error(errorMsg)
        })
      },
      /**
       * , 
       * @return {[type]} [description]
       */
      showEditDialog(editBoolean) {
        if(!!this.activeNode.parent_id) {
          this.dialogArticleEditVisible   = true
          editBoolean ? this.operateType = "edit" : this.operateType = "new"

          this.$nextTick(_ => {
            this.initEditor()
          })
        } else {
          this.$message.error("")
        }
      },
      initEditor () {
        this.editor = new E(this.$refs.editor)
        //
        this.editor.customConfig.debug = true
        this.editor.customConfig.uploadImgServer = `${this.common.basePath}/file/public/upload`
        this.editor.customConfig.showLinkImg = false
        this.editor.customConfig.uploadImgHeaders = this.common.returnConfig()
        this.$refs.editor ? this.editor.create() : null //  
        if(this.operateType == "edit") {
          this.editor.txt.html(this.editorForm.article_content)
        } else if (this.operateType == "new"){
          this.editor.txt.html("")
          this.editorForm.article_name = ""
        }
      },
      /**
       * , 
       * @return {[type]} [description]
       */
      submitArticle() {
        let config = {
          method: this.operateType == "new" ? "post" : "patch",
          url: this.operateType == "new" ? `${this.common.basePath}/article/save` : `${this.common.basePath}/article/${this.editorForm.id}`,
          data: {
            article_name:    this.editorForm.article_name,
            article_content: this.editor.txt.html(), //
            types:           [this.activeId],
            author:          JSON.parse(sessionStorage.getItem("Access-Key"))
          }
        }
        this.pageLoading = true
        this.axios(config).then((response) => {
          this.$message.success(response.data.meta.message)
          this.dialogArticleEditVisible = false
          this.pageLoading = false
          this.getArticleList(this.activeCode)
        }).catch((errorMsg) => {
          this.pageLoading = false
          this.$message.error(errorMsg)
        })
      },
      /**
       * 
       * @param  {object} row row
       * @return {[type]}     [description]
       */
      getArticleDetail(row) {
        let config = {
          method: "get",
          url: `${this.common.basePath}/article/${row.id}`,
        }
        this.axios(config).then((response) => {
          this.editorForm = response.data.data
          this.dialogArticleDetailVisible = true
        }).catch((errorMsg) => {
          this.$message.error(errorMsg)
        })
      },
      /**
       *   form
       * @return {[type]} [description]
       */
      changeToEdit() {
        this.dialogArticleDetailVisible = false
        this.showEditDialog(true)
        let newForm = JSON.parse(JSON.stringify(this.editorForm))
        this.editorForm = newForm
      },
      /**
       * 
       * @param  {[type]} row [description]
       * @return {[type]}     [description]
       */
      deleteArticle(row) {
        this.$confirm("", "?", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(_ => {
          let config = {
            method: "patch",
            url: `${this.common.basePath}/article/delete/${row.id}`,
          }
          this.axios(config).then((response) => {
            this.$message.success(response.data.meta.message)
            this.getArticleList(this.activeCode)
          }).catch((errorMsg) => {
            this.$message.error(errorMsg)
          })
        }).catch(error => {})
      },
      pageSizeChange(val) {
        this.pageSize = val
        this.getArticleList(this.activeCode)
      },
      pageCurrentChange(val) {
        this.pageNum = val
        this.getArticleList(this.activeCode)
      },
      //////
      // TODO  ( > ) 
      /**
       * 
       * @param  {Object} draggingNode 
       * @param  {Object} dropNode     
       * @param  {String} type          'prev''inner'  'next'
       * @return {[type]}              [description]
       */
      allowDrop(draggingNode, dropNode, type) {
        let self = this
        if(!dropNode.data.parent_id) {//
          switch(type) {
            case "prev":
              return true;
              break;
            case "next":
              return true;
              break;
            case "inner":
              if(!!draggingNode.data.childrens && draggingNode.data.childrens.length > 0) { //, 
                return false
              } else {
                return true
              }
              break;
          }
        } else { //
          switch(type) {
            case "prev":
              if(!!draggingNode.data.childrens && draggingNode.data.childrens.length > 0) { //, 
                return false
              } else {
                return true
              }
              break;
            case "next":
              if(!!draggingNode.data.childrens && draggingNode.data.childrens.length > 0) { //, 
                return false
              } else {
                return true
              }
              break;
            case "inner": //inner
              return false
          }
        }
      },
      /**
       *   
       * @param  {[type]} draggingNode [description]
       * @param  {[type]} dropNode     [description]
       * @param  {[type]} dropType     [description]
       * @param  {[type]} ev           [description]
       * @return {[type]}              [description]
       */
      handleDragEnd(draggingNode, dropNode, dropType, ev) {
        let config = {
          method: "post",
          url: `${this.common.basePath}/article_type/tree/reset`,
          data: this.treeData
        }
        this.pageLoading = true
        this.axios(config).then((response) => {
          this.initArticleTree()
        }).catch((errorMsg) => {
          this.initArticleTree()
          this.$message.error(errorMsg)
        })
        
      },
    }
  };
</script>